import React, { Component } from 'react';
import axios from 'axios';


export default class ShowPost extends Component {
    constructor(props) {
        super(props);
        this.state = {posts: []};
    }
    componentDidMount() {
        axios.get('https://secure-dusk-80764.herokuapp.com/posts/show')
            .then(response => {
                this.setState(
                    { posts: response.data }
                );
            })
            .catch(function (error){
                console.log(error);
            })
    }
    render() {
        return (
            <div>
                {this.state.posts}
            </div>
        )
    }
}